import * as d3 from 'd3';
const fs = require('fs');

const datasetFolder = './datasets/';

export function loadDatasets() {
    const fileNames = [];
    fs.readdirSync(datasetFolder, (err, files) => {
        files.forEach((file) => {
            fileNames.push(file);
        });
    });
    console.log(fileNames);
}
