import * as d3 from 'd3';
import * as $ from 'jquery';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-slider';
import 'bootstrap-slider/dist/css/bootstrap-slider.min.css';
import { registerJQueryD3Click } from './tools/helpers';
import { loadDatasets } from './tools/data-manager';

registerJQueryD3Click();

// global variables
const visContainer = d3.select('#vis-container');

loadDatasets();

const margins = {
    top: 30,
    right: 50,
    bottom: 50,
    left: 80,
};

const width = 1200;
const height = 800;
const visWidth = width - margins.left - margins.right;
const visHeight = height - margins.top - margins.bottom;

const svg = visContainer.append('svg')
    .attr('width', width)
    .attr('height', height);

let viewport = svg.append('g')
    .attr('transform', `translate(${margins.left},${margins.top})`);


d3.select('#file-dropdown')
    .on('change', () => {
        viewport.remove();
        viewport = svg.append('g')
            .attr('transform', `translate(${margins.left},${margins.top})`);
        const selectedText = d3.select('#file-dropdown option:checked').text();
        const selectedFile = d3.select('#file-dropdown').node().value;
        d3.select('#selected-dropdown').text(selectedText);
        const csvFileName = `/static/datasets/${selectedFile}_norm.csv`;
        const dataPoints = []; let cindex;

        // parse csv file
        d3.csv(csvFileName).then((data) => {
            const headers = data.columns;
            const numRows = data.length;
            cindex = headers[0] === '' ? 1 : 0;
            for (let i = 0; i < numRows; i++) {
                const obj = {
                    [headers[cindex]]: data[i][headers[cindex]],
                    [headers[cindex + 1]]: data[i][headers[cindex + 1]],
                    vid: i + 1,
                    class: data[i][headers[headers.length - 1]],
                };
                dataPoints.push(obj);
            }

            const classes = Array.from(new Set(data.map(d => d.class)));

            const visualization = viewport.append('g');
            const color = d3.scaleOrdinal().domain(classes).range(['red', 'green', 'blue']);

            const dim1X = d3.scaleLinear()
                .domain(d3.extent(dataPoints.map(x => x[headers[cindex]])))
                .range([0, visWidth]).nice();

            const dim2Y = d3.scaleLinear()
                .domain(d3.extent(dataPoints.map(x => x[headers[cindex + 1]])))
                .range([visHeight, 0]).nice();

            visualization.append('g')
                .attr('class', 'x-axis')
                .attr('transform', `translate(0,${visHeight})`)
                .call(d3.axisBottom(dim1X));

            visualization.append('g')
                .attr('class', 'left-axis')
                .call(d3.axisLeft(dim2Y));

            visualization.append('text').text(headers[cindex])
                .attr('text-anchor', 'end')
                .attr('x', visWidth / 2)
                .attr('y', visHeight + margins.top);

            visualization.append('text').text(headers[cindex + 1])
                .attr('text-anchor', 'end')
                .attr('transform', 'rotate(-90)')
                .attr('x', -visHeight / 2 + margins.bottom)
                .attr('y', -margins.right + 10);

            const tooltip = visContainer.append('div')
                .attr('class', 'tooltip')
                .style('opacity', 0)
                .style('background-color', 'white')
                .style('color', 'black')
                .style('border', 'solid')
                .style('border-width', '4px')
                .style('border-radius', '6px')
                .style('padding', '10px')
                .style('position', 'absolute');

            const mouseover = function (d) {
                tooltip
                    .style('opacity', 1)
                    .style('border-color', color(d.class));
                d3.select(this)
                    .style('stroke', 'black')
                    .style('opacity', 1);
            };

            const mousemove = function (d) {
                tooltip
                    .html(`Class: ${d.class}<br>ID: ${d.vid
                    // eslint-disable-next-line no-bitwise
                    }<br>ScreenPosition: (${~~d3.select(this).attr('cx')}, ${~~d3.select(this).attr('cy')})`
                    + `<br>DataPoint: (${d[headers[cindex]]}, ${d[headers[cindex + 1]]})`)
                    .style('left', `${d3.mouse(this)[0] + 90}px`)
                    .style('top', `${d3.mouse(this)[1] + 60}px`);
            };

            const mouseleave = function () {
                tooltip
                    .style('opacity', 0);
                d3.select(this)
                    .style('stroke', 'none')
                    .style('opacity', 0.8);
            };

            visualization.selectAll('dot')
                .data(dataPoints)
                .enter()
                .append('circle')
                .attr('cx', d => dim1X(d[headers[cindex]]))
                .attr('cy', d => dim2Y(d[headers[cindex + 1]]))
                .attr('r', '5')
                .style('fill', d => color(d.class))
                .style('stroke', 'none')
                .style('opacity', 0.8)
                .on('mouseover', mouseover)
                .on('mousemove', mousemove)
                .on('mouseleave', mouseleave);
        });
    });
